.textSection svg {
  max-width: 370px;
  font-family: "Aharoni";
}
.textSection svg text {
  fill: transparent;
  stroke: #fff;
  stroke-width: 1px;
  stroke-dasharray: 400px;
  stroke-dashoffset: 400px;
  animation: logoAnim 2s linear;
  animation-fill-mode: forwards;
}

.textSection svg#helloText text {
  animation: logoAnim 5s linear;
  animation-fill-mode: forwards;
}
.textSection svg#nameText text {
  animation: logoAnim 4s linear;
  animation-fill-mode: forwards;
}

#btn {
  background: rgba(34, 34, 34, 0.514);
  height: 50px;
  min-width: 150px;
  border: none;
  border-radius: 10px;
  color: #eee;
  font-size: 1.2rem;
  position: relative;
  transition: 0.5s;
  -webkit-tap-highlight-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding-top: 5px;
}

#btn #circle {
  width: 5px;
  height: 5px;
  /* background: transparent; */
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  overflow: hidden;
  transition: 500ms;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#btn:hover {
  /* background: rgba(34, 34, 34, 0.8); */
  border-radius: 10px 10px 0 0;
}

#btn:hover #circle {
  height: 50px;
  width: 150px;
  left: 0;
  border-radius: 0;
  border-bottom: 2px solid #eee;
}

@keyframes logoAnim {
  from {
    stroke-dashoffset: 400px;
  }
  90% {
    fill: transparent;
    stroke-width: 1px;
  }
  to {
    stroke-dashoffset: 0;
    fill: #fff;
    stroke-width: 0;
  }
}

